"use client"

import { useState } from "react"
import { Plus, Search, Edit2, Trash2, X, LayoutGrid, List } from "lucide-react"
import { mockColors } from "@/lib/mock-data"

const VIEW_MODE_KEY = "adminCoresViewMode"

type ViewMode = "list" | "grid"

export default function AdminCores() {
  const [colors, setColors] = useState(mockColors)
  const [searchTerm, setSearchTerm] = useState("")

  const [viewMode, setViewMode] = useState<ViewMode>(() => {
    if (typeof window === "undefined") return "list"
    const saved = window.localStorage.getItem(VIEW_MODE_KEY) as ViewMode | null
    return saved === "list" || saved === "grid" ? saved : "list"
  })

  const [showModal, setShowModal] = useState(false)
  const [editingColor, setEditingColor] = useState<(typeof mockColors)[0] | null>(null)
  const [formData, setFormData] = useState({
    nome: "",
    codigoHex: "#000000",
    ativo: true,
  })

  const filteredColors = colors.filter((c) =>
    c.nome.toLowerCase().includes(searchTerm.toLowerCase()),
  )

  const changeViewMode = (mode: ViewMode) => {
    setViewMode(mode)
    if (typeof window !== "undefined") {
      window.localStorage.setItem(VIEW_MODE_KEY, mode)
    }
  }

  const openCreateModal = () => {
    setEditingColor(null)
    setFormData({
      nome: "",
      codigoHex: "#000000",
      ativo: true,
    })
    setShowModal(true)
  }

  const openEditModal = (color: (typeof mockColors)[0]) => {
    setEditingColor(color)
    setFormData({
      nome: color.nome,
      codigoHex: color.codigoHex,
      ativo: color.ativo,
    })
    setShowModal(true)
  }

  const handleSave = () => {
    if (editingColor) {
      setColors(
        colors.map((c) =>
          c.id === editingColor.id
            ? {
                ...c,
                nome: formData.nome,
                codigoHex: formData.codigoHex,
                ativo: formData.ativo,
              }
            : c,
        ),
      )
    } else {
      const newColor = {
        id: (colors.length + 1).toString(),
        nome: formData.nome,
        codigoHex: formData.codigoHex,
        ativo: formData.ativo,
        createdAt: new Date().toISOString().split("T")[0],
      }
      setColors([...colors, newColor])
    }
    setShowModal(false)
  }

  const handleDelete = (id: string) => {
    if (confirm("Tem certeza que deseja excluir esta cor?")) {
      setColors(colors.filter((c) => c.id !== id))
    }
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Cores</h1>
          <p className="text-foreground/60">Gerencie as cores dos produtos</p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          <Plus size={18} />
          Nova Cor
        </button>
      </div>

      {/* Filters & View Toggle */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6 justify-between">
        <div className="relative flex-1 max-w-md">
          <Search
            size={18}
            className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/50"
          />
          <input
            type="text"
            placeholder="Buscar por nome..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
          />
        </div>
        <div className="flex items-center bg-muted rounded-lg p-1 border border-border">
          <button
            onClick={() => changeViewMode("list")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "list"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <List size={18} />
          </button>
          <button
            onClick={() => changeViewMode("grid")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "grid"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <LayoutGrid size={18} />
          </button>
        </div>
      </div>

      {/* Content */}
      {viewMode === "list" ? (
        <div className="bg-card border border-border rounded-xl overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-muted border-b border-border">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Cor
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Código Hex
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Status
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Criado em
                  </th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-foreground">
                    Ações
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-border">
                {filteredColors.map((color) => (
                  <tr
                    key={color.id}
                    className="hover:bg-muted/50 transition-colors"
                  >
                    <td className="px-6 py-4">
                      <div className="flex items-center gap-3">
                        <div
                          className="w-8 h-8 rounded-full border border-border shadow-sm"
                          style={{ backgroundColor: color.codigoHex }}
                        />
                        <span className="font-medium text-foreground">
                          {color.nome}
                        </span>
                      </div>
                    </td>
                    <td className="px-6 py-4 text-sm text-foreground/70 font-mono">
                      {color.codigoHex}
                    </td>
                    <td className="px-6 py-4">
                      <span
                        className={`px-2 py-1 rounded-full text-xs font-medium ${
                          color.ativo
                            ? "bg-green-100 text-green-700"
                            : "bg-gray-100 text-gray-700"
                        }`}
                      >
                        {color.ativo ? "Ativo" : "Inativo"}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-sm text-foreground/70">
                      {new Date(color.createdAt).toLocaleDateString("pt-BR")}
                    </td>
                    <td className="px-6 py-4">
                      <div className="flex items-center justify-center gap-2">
                        <button
                          onClick={() => openEditModal(color)}
                          className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                        >
                          <Edit2 size={16} />
                        </button>
                        <button
                          onClick={() => handleDelete(color.id)}
                          className="p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                        >
                          <Trash2 size={16} />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
          {filteredColors.map((color) => (
            <div
              key={color.id}
              className="group bg-card border border-border rounded-xl p-6 hover:shadow-lg transition-all duration-300"
            >
              <div className="flex justify-between items-start mb-4">
                <div
                  className="w-16 h-16 rounded-full border border-border shadow-sm"
                  style={{ backgroundColor: color.codigoHex }}
                />
                <div className="flex gap-2 opacity-0 group-hover:opacity-100 transition-opacity">
                  <button
                    onClick={() => openEditModal(color)}
                    className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                  >
                    <Edit2 size={16} />
                  </button>
                  <button
                    onClick={() => handleDelete(color.id)}
                    className="p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                  >
                    <Trash2 size={16} />
                  </button>
                </div>
              </div>
              <h3 className="font-medium text-foreground text-lg mb-1">
                {color.nome}
              </h3>
              <p className="text-sm text-foreground/60 font-mono mb-3">
                {color.codigoHex}
              </p>
              <div className="flex items-center justify-between mt-4 pt-4 border-t border-border">
                <span
                  className={`px-2 py-1 rounded-full text-xs font-medium ${
                    color.ativo
                      ? "bg-green-100 text-green-700"
                      : "bg-gray-100 text-gray-700"
                  }`}
                >
                  {color.ativo ? "Ativo" : "Inativo"}
                </span>
                <span className="text-xs text-foreground/40">
                  {new Date(color.createdAt).toLocaleDateString("pt-BR")}
                </span>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-md">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <h2 className="text-xl font-medium text-foreground">
                {editingColor ? "Editar Cor" : "Nova Cor"}
              </h2>
              <button
                onClick={() => setShowModal(false)}
                className="text-foreground/60 hover:text-foreground"
              >
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Nome da Cor
                </label>
                <input
                  type="text"
                  value={formData.nome}
                  onChange={(e) =>
                    setFormData({ ...formData, nome: e.target.value })
                  }
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  placeholder="Ex: Azul Marinho"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Código Hex
                </label>
                <div className="flex gap-3">
                  <input
                    type="color"
                    value={formData.codigoHex}
                    onChange={(e) =>
                      setFormData({ ...formData, codigoHex: e.target.value })
                    }
                    className="h-10 w-14 p-1 border border-border rounded-lg bg-background cursor-pointer"
                  />
                  <input
                    type="text"
                    value={formData.codigoHex}
                    onChange={(e) =>
                      setFormData({ ...formData, codigoHex: e.target.value })
                    }
                    className="flex-1 px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent uppercase"
                    placeholder="#000000"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Status
                </label>
                <div className="flex items-center gap-2">
                  <input
                    type="checkbox"
                    id="ativo"
                    checked={formData.ativo}
                    onChange={(e) =>
                      setFormData({ ...formData, ativo: e.target.checked })
                    }
                    className="w-4 h-4 rounded border-border text-primary focus:ring-primary"
                  />
                  <label
                    htmlFor="ativo"
                    className="text-sm text-foreground cursor-pointer"
                  >
                    Ativo
                  </label>
                </div>
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors"
              >
                {editingColor ? "Salvar Alterações" : "Criar Cor"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}